.form-program {
  padding: 28px 20px 25px;
  border-bottom: 1px solid $border-text-grey;
  margin-bottom: 26px;

  @media (min-width: $tablet) {
    border: none;
    padding: 0 67px 72px 70px;
    margin: 0;
  }

  @media (min-width: $desktop) {
    padding: 0;
    flex-basis: 50%;
  }
}

.form-program__list {
  @media (min-width: $tablet) {
    border: 1px solid $border-text-grey;
    padding: 25px 28px 29px;
  }
}

.form-program__item {
  &:not(:last-child) {
    padding-bottom: 25px;
  }
}

.form-program__input {
  &:checked ~ .form-program__label::after {
    display: block;
  }

  &:disabled ~ .form-program__label {
    opacity: 0.3;
    pointer-events: none;
  }
}

.form-program__label {
  padding-left: 45px;
  position: relative;
  cursor: pointer;

  &::before {
    content: "";
    width: 30px;
    height: 30px;
    background-color: $white;
    border: 1px solid $border-control-grey;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
  }

  &::after {
    content: "";
    width: 14px;
    height: 14px;
    background-color: $border-green;
    border-radius: 50%;
    position: absolute;
    top: 50%;
    left: 8px;
    transform: translateY(-50%);
    display: none;
  }

  &:hover::before {
    border: 1px solid $radio-hover-border-grey;
  }
}

.form-program__input:focus ~ .form-program__label::before {
  border: 2px solid $black;
}
